import React from 'react';
import ReactDOM from 'react-dom';  
import { Button, Flex, WingBlank, ActivityIndicator } from 'antd-mobile';
var title = '@f(x) = \\sqrt{mx^2 + mx + 1}@Rm?';
var answers = [
      { correct: 0, value: '@0 < m < 4@' },
      { correct: 1, value: '@0 \\leq m \\leq 4@' },
      { correct: 0, value: '@m \\geq 4@' },
      { correct: 0, value: '@0 < m \\leq 4@'}
    ];
var breakdown = [
      { sn: 0, value: 'f(x)R,@mx^2 + mx + 1 \\geq 0@', kpid: '' },
      { sn: 1, value: '@m \\neq 0@, @y = mx^2 + mx + 1@x@\\Delta = m^2 -4m \\leq 0@, m > 0', kpid: '' },
      { sn: 2, value: '@0 < m \\leq 4@', kpid: '' },
      { sn: 3, value: 'm = 0f(x) = 1@0 \\leq m \\leq 4@', kpid: '' },
    ];

var problems = { title: title, answers: answers, type: 1, breakdown: breakdown };

var title1 = 'f(x)@2f(x) + 3f(-x) = 3x + 4@f(x)=';
var breakdown1 = [{sn: 0, value: '2f(x) + 3f(-x) = 3x + 4 (1)2f(-x) + 3f(x) = -3x + 4 (2)(1)(2)@f(x) = 3x + 4/3@'}];
var answers1 = {value:'@3x + \\frac{4}/{3}@'};
var problem1 = { title: title1, type: 2, answers: answers1, breakdown: breakdown1 };
class Welcome extends React.Component {
  constructor(props, context) { 
      super(props,context);
      console.log(context);
      this.state = {isLoading: false};
  }
  isLoading(){
      console.log("set isLoading = true");
      this.setState({isLoading: true}); 
  }
  loadTestData(){
      this.setState({isLoading: false});
  }
  onStart(e){
      this.isLoading();
      setTimeout(() => {
        this.setState({isLoading: false});
        var history = this.context.router.history;
        var exercise = [problems, problem1];
        var state = { exindex: 0, exercise: exercise };
        history.push("/mobile-test/Question", state);
      }, 5000);
  }
  render(){
    return (
      <WingBlank>
        <ActivityIndicator toast animating={this.state.isLoading} />
        <div style={{ margin: '8rem 0 0 0'}} className="btn-container">
          <div>
            <Button className="btn" type="ghost" onClick={e => this.onStart(e)}></Button>
          </div>
        </div>
      </WingBlank>
    )  
  }
}
export default Welcome;
//ReactDOM.render(<Welcome />, document.getElementById('root'));




